.cardWrapper {
  max-width: 1100px;
  background: #d7eda8;
  border-radius: 10px;
  border: 2px solid #0085a3;
  padding: 20px;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.25);
  text-align: center;

  display: flex;
  flex-direction: column;
}

#amountOfQuestions,
#difficulty,
#category,
#type {
  text-align: center;
}